/* 비활성화된 메뉴 아이템 스타일 */
.main-menu-with-disabled .ant-menu-item-disabled {
  color: #999 !important;
  cursor: not-allowed !important;
}

.main-menu-with-disabled .ant-menu-item-disabled:hover {
  color: #999 !important;
  background-color: transparent !important;
}

.main-menu-with-disabled .ant-menu-item-disabled .ant-menu-title-content {
  color: #999 !important;
}

.main-menu-with-disabled .ant-menu-item-disabled .anticon {
  color: #999 !important;
}

/* 비활성화된 메뉴의 링크 스타일 */
.main-menu-with-disabled .ant-menu-item-disabled a {
  color: #999 !important;
  cursor: not-allowed !important;
  pointer-events: none !important;
}

.main-menu-with-disabled .ant-menu-item-disabled a:hover,
.main-menu-with-disabled .ant-menu-item-disabled a:focus,
.main-menu-with-disabled .ant-menu-item-disabled a:active {
  color: #999 !important;
  text-decoration: none !important;
}

