body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

/* xs 화면에서 ag-Grid 페이징 패널 줄바꿈 및 크기 조정 */
@media (max-width: 576px) {
  /* 패널이 한 줄을 넘기면 줄바꿈 → flex-direction: column으로 변경 */
  .ag-paging-panel {
    display: flex;
    flex-direction: column;
    align-items: stretch;
    gap: 4px 0;
    padding: 4px 8px;
    font-size: 12px;
    width: 100%;
    box-sizing: border-box;
  }
  /* 긴 텍스트가 줄바꿈될 수 있도록 */
  .ag-paging-panel .ag-paging-description,
  .ag-paging-panel .ag-paging-row-summary-panel {
    white-space: normal;
    width: 100%;
  }
  /* 페이지 사이즈 선택을 상단으로 배치하여 공간 확보 (선택) */
  .ag-paging-panel .ag-paging-page-size {
    order: -1;
    width: 100%;
  }
  /* 버튼 영역을 항상 보이도록 분리 및 정렬 */
  .ag-paging-panel .ag-paging-button,
  .ag-paging-panel button {
    margin-top: 4px;
    align-self: flex-end;
    min-width: 32px;
    padding: 0 6px;
  }
  /* 패널 전체에 가로 스크롤 허용 (필요시) */
  .ag-paging-panel {
    overflow-x: auto;
  }
}
