.management-header-card {
  border-radius: 8px;
  background: #f8fafc; /* light fallback */
  border: 1px solid #e5e7eb;
  box-shadow: 0 1px 2px rgba(0,0,0,0.04);
  transition: background .25s, border-color .25s;
}
.management-header-row {
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-wrap: wrap;
  gap: 12px;
  margin-bottom: 4px;
}
.management-header-title {
  margin: 0 !important;
  font-weight: 600 !important;
  display: flex;
  align-items: center;
  gap: 8px;
  flex-shrink: 1;
  min-width: 0;
}
.management-header-title .icon-wrapper {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  font-size: 22px;
  color: #1677ff;
  flex-shrink: 0;
}
.management-header-actions {
  display: flex;
  gap: 8px;
  flex-wrap: wrap;
  align-items: center;
}
.management-header-desc { 
  display:block; 
}

/* 모바일 반응형 */
@media (max-width: 768px) {
  .management-header-row {
    flex-direction: column;
    align-items: flex-start;
  }
  .management-header-title {
    font-size: 18px !important;
  }
  .management-header-title .icon-wrapper {
    font-size: 18px;
  }
  .management-header-actions {
    width: 100%;
    justify-content: flex-start;
  }
  .management-header-desc {
    font-size: 13px;
  }
}

/* 다크모드 지원: prefers-color-scheme 및 data-theme 동시 대응 */
@media (prefers-color-scheme: dark) {
  .management-header-card { background:#1f1f1f; border-color:#303030; }
  .management-header-title { color:#e6f4ff !important; }
  .management-header-desc { color:#94a3b8 !important; }
}
body[data-theme='dark'] .management-header-card { background:#1f1f1f; border-color:#303030; }
body[data-theme='dark'] .management-header-title { color:#e6f4ff !important; }
body[data-theme='dark'] .management-header-desc { color:#94a3b8 !important; }

